import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'  
import defaultImg from '../../../static/img/kongjianzhan-wkt@2x.png' 
export default{
	name:"news_collection",
	data(){
		return{
			nav_header: [
				{text: '', icon:backImg, type:'backs', class:'header_left'},
				{text: '', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'', class:'header_right'},
			], 
			type:0, //0. 1. 
			text:null, //
			btnTitle:null, //
			defaultImg:defaultImg, //
			tip:"", //
			product_id: null,
		}
	},
	mounted () { 
  	},
	components: {header_nav},
	created(){ 
		this.type = this.$route.query.type;
		this.text = this.$route.query.text;
		this.btnTitle = this.$route.query.btnTitle;
		this.product_id = this.$route.query.product_id;
		if(this.type==0){
			this.nav_header[1].text ="";
			this.tip = "~";
		}else{ 
			this.nav_header[1].text =""; 
			this.tip = "~";
		};;
		
		this.$http.get(process.env.BASE_URL+"/apps/member/personSpace").then(function(res){
			console.log(res)
			this.payUrl = res.body.list.fail.button.real_href;
		})
	},
	methods:{
		backs(){
			this.$router.push({path: "/member_index"})
		},
		payment(){
			// if(this.type==0){
			// 	this.$router.push({path: "/personal_space"}); //
			// }else{
			// 	this.$router.push({path: "/company_space"}); //
			// }
			location.href= this.payUrl
		}
	}
}



