
import Swiper from 'vux/src/components/swiper/swiper.vue'

import header_nav from '@/components/template/header'

export default{
name: "index",
data(){
return {
msg_num			   : ""   ,	   //
nav_header         :  []  ,    //
section_datas_one  :  []  ,    //
section_datas_two  :  []  ,    //
carousel_list_data :  []  ,    //
header_class       :  ["header_left","header_middle","header_right"]  ,    //class
navIndex           :  0,		//
footerList         :  [], 	//
}
},
components: {Swiper, header_nav},

created(){
var that = this ;
console.log(process.env)
this.$http.get(process.env.BASE_URL+"/apps/index/index").then(function(res){
console.log(res)
that.msg_num = res.body.list.member_info.message_count;
that.nav_header = res.data.list.navigation.navigation_items //

that.nav_header.forEach(function (el,index) {
that.nav_header[index].class = that.header_class[index]
})
that.section_datas_one = res.data.list.section[2].section_datas //
that.section_datas_two = res.data.list.section[1].section_datas //
that.carousel_list_data = res.data.list.section[0].section_datas //
});
},
methods:{

message(){
location.href = this.nav_header[2].real_href;
},

linkType : function (link_href) {
location.href = link_href
},

good_link : function (id,type,index) {
this.$router.push({path: 'supply_demand', query:{type: type, category:id, index:index}})
},
tabClick(title) {
console.log(title=="")
if(title==""){
this.$router.push({path:"/publish_supply", query:{type:1}})
}else if(title==""){
this.$router.push({path:"/surround"})
}else if(title==""){
this.$router.push({path:"/publish_goods", query:{type:2}})
}
}
}
}



