import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'


import login from '@/components/login/login'              //
import register from '@/components/login/register'        //
import forgotten from '@/components/login/forgotten'      //

import BtmNav from '@/components/has_footer'
//

import Index from '@/components/index/index'
import IndexMap from '@/components/index/index_map'
import IndexMsg from '@/components/index/index_message'
import SupplyDemand from '@/components/index/supply_demand'
import Surround from '@/components/index/surround'
import Search from '@/components/index/search'
import InfoDetail from '@/components/index/info_detail'
import PublishSupply from '@/components/index/publish_supply'
import PublishGoods from '@/components/index/publish_goods'
//
import DiscoverIndex from '@/components/discover/index'
import Publish from '@/components/discover/publish'
import DetailMessage from '@/components/discover/detail_message'
import DetailNews from '@/components/discover/detail_news'
import CommentDetail from '@/components/discover/comment_detail'
//
import SpanceIndex from '@/components/space/index'
import SpaceDetail from '@/components/space/space_detail'
import CompanyIntro from '@/components/space/company_intro'
import CompanySupply from '@/components/space/company_supply'
import CompanyAlbum from '@/components/space/company_album'
import CompanyAlbumModify from '@/components/space/company_album_modify'
import CompanyNews from '@/components/space/company_news'
import PersonalSpaceEdit from '@/components/space/personal_space_edit' //
import CompanySpaceEdit from '@/components/space/company_space_edit' //

//
import MemberIndex from '@/components/member/member_index'  //
import CompanySpaceport from '@/components/member/company_spaceport'  //
import NewsCollection from '@/components/member/news_collection' //
import NewsDetail from '@/components/member/news_detail' //
import SupplyDemandColl from '@/components/member/supply_demand_coll' //
import SpaceCollection from '@/components/member/space_collection' //
import UpUserInfo from '@/components/member/up_user_info' //
import PersonalSpace from '@/components/member/personal_space' //
import NotOpenSpace from '@/components/member/not_open_space' //
import NotPayment from '@/components/member/not_payment' //
import PublishSupplyDemand from '@/components/member/publish_supply_demand' //
import AboutApp from '@/components/member/about_app' //app
import AboutUs from '@/components/member/about_us' //
import CPersonList from '@/components/member/cperson_list' //
import CPersonDetail from '@/components/member/cperson_detail' //
import MyPublish from '@/components/member/my_publish' //
import Feedback from '@/components/member/feedback' //



Vue.use(Router)
Vue.use(Resource)
 
export default new Router({
  routes: [
	{path:'*', redirect: '/index' },
  	{path: '/login', name: 'login', component: login },									//
  	{path: '/register', name: 'register', component: register },						//
  	{path: '/forgotten', name: 'forgotten', component: forgotten },						//
  	
  	{path: '/btm_nav', name: 'BtmNav', component: BtmNav, children: [
		{path: '/index', name: 'Index', component: Index},								//
		{path: '/discover_index', name: 'DiscoverIndex', component: DiscoverIndex },	//
		{path: '/spance_index', name: 'SpanceIndex', component: SpanceIndex },			//
		{path: '/member_index', name: 'member_index', component: MemberIndex },			//
  	]},
  	//
    {path: '/index_map', name: 'IndexMap', component: IndexMap },						//
    {path: '/index_message', name: 'IndexMsg', component: IndexMsg },					//
    {path: '/supply_demand', name: 'Supply_demand', component: SupplyDemand },			//
    {path: '/surround', name: 'Surround', component: Surround },						//
    {path: '/search', name: 'Search', component: Search },								//
    {path: '/info_detail', name: 'InfoDetail', component: InfoDetail },					//
    {path: '/publish_supply', name: 'PublishSupply', component: PublishSupply },		//
    {path: '/publish_goods', name: 'PublishGoods', component: PublishGoods },			//
    //
    {path: '/publish', name: 'Publish', component: Publish },							//
    {path: '/detail_message', name: 'DetailMessage', component: DetailMessage },		//
    {path: '/detail_news', name: 'DetailNews', component: DetailNews },					//
    {path: '/comment_detail', name: 'CommentDetail', component: CommentDetail },		//
    //
    {path: '/space_detail', name: 'SpaceDetail', component: SpaceDetail },				//
    {path: '/company_intro', name: 'CompanyIntro', component: CompanyIntro },			//
    {path: '/company_supply', name: 'CompanySupply', component: CompanySupply },		//
    {path: '/company_album', name: 'CompanyAlbum', component: CompanyAlbum },			//
    {path: '/company_album_modify', name: 'CompanyAlbumModify', component: CompanyAlbumModify },	//
    {path: '/company_news', name: 'CompanyNews', component: CompanyNews },        //
    {path: '/personal_space_edit', name: 'PersonalSpaceEdit', component: PersonalSpaceEdit },        //
    {path: '/company_space_edit', name: 'CompanySpaceEdit', component: CompanySpaceEdit },        // 
    
    //
    {path: '/company_spaceport', name: 'CompanySpaceport', component: CompanySpaceport }, //
    {path: '/news_collection', name: 'NewsCollection', component: NewsCollection }, 	//
    {path: '/news_detail', name: 'NewsDetail', component: NewsDetail }, //
    {path: '/supply_demand_coll', name: 'SupplyDemandColl', component: SupplyDemandColl }, //
    {path: '/space_collection', name: 'SpaceCollection', component: SpaceCollection }, //
    {path: '/up_user_info',name:'UpUserInfo',component:UpUserInfo},//
    {path: '/personal_space',name:'PersonalSpace',component:PersonalSpace},//
    {path: '/not_open_space',name:'NotOpenSpace',component:NotOpenSpace},//
    {path: '/not_payment',name:'NotPayment',component:NotPayment},//
    {path: '/publish_supply_demand',name:'PublishSupplyDemand',component:PublishSupplyDemand},//
    {path: '/about_app',name:'AboutApp',component:AboutApp},//app
    {path: '/about_us',name:'AboutUs',component:AboutUs},//
    {path: '/cperson_list',name:'CPersonList',component:CPersonList},//
    {path: '/cperson_detail',name:'CPersonDetail',component:CPersonDetail}, //
    {path: '/my_publish',name:'MyPublish',component:MyPublish},//
    {path: '/feedback',name:'Feedback',component:Feedback},//
  ]
})



