
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default{
name: "detail",
data(){
return{
item_id     : null,				//id
item_type   : null,				//
category    : null,				//
header_img  : null,				//
nick_name   : null,				//
img_detail  : null,				//
beginAddress: null,				//
endAddress  : null,				//
user_id     : null,				//id
collectCode : null,				//
collectTitle: null,				//
focusCode   : null,				//
focusTitle  : null,				//
link_url    : null,				//
nav_header:[
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'link', class:'header_right'},
],

group_data1 : [
{title: "", value: ""},
{title: "", value: ""},
{title: "" , value: ""},
{title: "", value: "", type: "phone"},
],								//
group_data2 : [
{title: "", value: "", type:"input"},
{title: "", value: "", type:"input"},
{title: "", value: "", type:"textarea"},
],								//
group_data3 : [
{title: "", value: ""},
{title: "", value: ""},
{title: "", value: ""},
{title: "", value: ""},
],								//
group_data4 : [
{title: "", value: ""},
{title: "", value: ""},
],								//

group_data5 : [
{title: "", value: ""},
{title: "", value: ""},
{title: "", value: ""},
{title: "", value: ""},
],								//
group_data6 : [
{title: "", value: ""},
{title: "", value: "", type: "phone"},
],								//
group_data7 : [
{title: "", value: ""},
{title: "", value: ""},
],								//
group_data8 : [
{title: "", value: ""},
{title: "", value: ""},
{title: "", value: ""},
],								//
group_data9 : [
{title: "", value: ""},
{title: "", value: ""},
],								//
}
},
components: {header_nav,Group, Cell, CellBox, XTextarea},
created(){
console.log(this.$route.query)
this.item_id = this.$route.query.id;
this.item_type = this.$route.query.type;
this.$http.get(process.env.BASE_URL+"/apps/supply/detail",{
params:{
id: this.item_id
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
this.category = res.body.list.category;			//
this.header_img = res.body.list.head_pic;		//
this.nick_name = res.body.list.username;		//
this.img_detail = res.body.list.image			//
this.user_id = res.body.list.uid;				//id
this.collectCode = res.body.list.collected;		//
this.collectTitle = res.body.list.title;		//
this.focusCode = res.body.list.followed;		//
this.link_url = res.body.list.real_href;		//

if(this.item_type==1){
this.beginAddress = res.body.list.regions1;
this.endAddress = res.body.list.regions2;

this.group_data1[0].value = res.body.list.title;
this.group_data1[1].value = res.body.list.company;
this.group_data1[2].value = res.body.list.name;
this.group_data1[3].value = res.body.list.mobile;

if(this.category==122){
this.group_data2[0].value = res.body.list.regions1+""+res.body.list.address;
this.group_data2[1].value = res.body.list.depot_area+"";
this.group_data2[2].value = res.body.list.depot_content;
}

if(this.category==124){
this.group_data3[0].value = res.body.list.car_number;
this.group_data3[1].value = res.body.list.car_load+"";
this.group_data3[2].value = res.body.list.max_load+"";
this.group_data3[3].value = res.body.list.time1+"  "+res.body.list.time2;
}

if(this.category==118||this.category==119||this.category==120||this.category==121){
this.group_data4[0].value = res.body.list.max_load+"";
this.group_data4[1].value = res.body.list.time1+"  "+res.body.list.time2;
}
}else if(this.item_type==2){

this.group_data5[0].value = res.body.list.title;
this.group_data5[1].value = res.body.list.goods_name;
this.group_data5[2].value = res.body.list.is_danger==1?"":"";
this.group_data5[3].value = res.body.list.goods_num;

this.group_data6[0].value = res.body.list.name;
this.group_data6[1].value = res.body.list.mobile;

this.group_data7[0].value = res.body.list.time1;
this.group_data7[1].value = res.body.list.regions1+" "+res.body.list.address;

if(this.category==122){
this.group_data8[0].value = res.body.list.category_name;
this.group_data8[1].value = res.body.list.depot_area+"";
this.group_data8[2].value = res.body.list.depot_content;
}else{
this.group_data9[0].value = res.body.list.category_name;
this.group_data9[1].value = res.body.list.depot_content;
}
}
}
})
},
methods:{

link(){
location.href=this.link_url;
},

toSpace(){
console.log("")
},

collect_focus(type, id, title, code){
var Url;
if(code==0){
Url = process.env.BASE_URL+"/apps/general/collectAdd";
}else{
Url = process.env.BASE_URL+"/apps/general/collectDel"
}

this.$http.post(Url,{
aid	  : id,
title : title,
favorite_category: type,
},{emulateJSON:true}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
if(code==0){
if(type==6){
this.collectCode = 1;
}else{
this.focusCode = 1
}
}else{
if(type==6){
this.collectCode = 0;
}else{
this.focusCode = 0
}
}
}
})
},

call(type, number){

if(type=='phone'){
location.href="tel:"+number;
}
}
}
}



