import header_nav from '@/components/template/header'
export default{
	name: "member_index",
	data(){
		return{
			type                    : null   ,
			back_img                : ""     ,    //
			head_pic                : ""     ,    //
			username                : ""     ,    //
			company_name            : ""     ,    //
			msg_num					: ""     ,	  //
			section_items_one       : []     ,    //
			section_title_two       : ""     ,    //
			section_title_three     : ""     ,    //
			my_collect_data         : []     ,    //
			my_spaceport_data       : []     ,    //
			my_App_data             : []     ,    //App
			uid						: null   ,    //
			nav_header              : [
				{text: '', icon:"", type:'backs', class:'header_left'},
				{text: '', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'message', class:'header_right'},
			]                                ,    //
			message_url             : null,		  //
		}
	},
	components: {header_nav},
	created(){
		var that = this ;
		this.$http.get(process.env.BASE_URL+"/apps/member/index").then(function(res){
			if(res.body.code=="40000"){
				that.uid = res.body.list.member_info.uid;
				that.message_url = res.body.list.section["0"].section_items["0"].real_href;
				that.nav_header[2].icon    =   res.body.list.section["0"].section_items["0"].icon;
				that.back_img              =   res.body.list.section["0"].section_bgimg;
				that.head_pic              =   res.body.list.member_info.head_pic;
				that.username              =   res.body.list.member_info.username;
				that.company_name          =   res.body.list.member_info.company_name;
				that.msg_num               =   res.body.list.member_info.message_count;
				that.section_items_one     =   res.body.list.section[1].section_items;
				that.section_title_two     =   res.body.list.section[2].section_title;
				that.my_collect_data       =   res.body.list.section[2].section_items;
				that.section_title_three   =   res.body.list.section[3].section_title;
				that.my_spaceport_data     =   res.body.list.section[3].section_items;
				that.my_App_data           =   res.body.list.section[4].section_items;
			}else{
				this.$router.push({ path: "/login", query:{app: "login"}})
			}
		});
	},
	methods:{
		message(){
			location.href=this.message_url;
		},
		space(index){
			switch(index){
				case 0: //
					this.companySpace(); 
					break;
				case 1: //
					this.personSpace(); 
					break;
				case 2: //
					this.$router.push({path:"publish_supply_demand"});
					break;
				case 3: // 
					this.$router.push({path:"company_news",query:{uid:this.uid}});
					break;
			} 
		},
		goCollection(index){ 
			switch(index){
				case 0:
					this.$router.push({path: "/news_collection"});
					break;
				case 1:
					this.$router.push({path: "/supply_demand_coll"});
					break;
				case 2:
					this.$router.push({path: "/space_collection"});
					break;
			}
		},
		/**
		 * 
		 * @param  0  1. 
		 */
		goLocation(index){
			switch(index){
				case 0:
					this.$router.push({path:"/supply_demand",query:{type:2,category:2,index:0,uid:this.uid}});
					break;
				case 1:
					this.$router.push({path:"/cperson_list"});
					break;
			}
			
		},
		/**
		 * app 
		 */
		goAboutApp(){
			this.$router.push({path:"/about_app"});
		},
		goUserInfo(){
			this.$router.push({path:"/up_user_info",query:{uid:this.uid}});
		},
		/**
		 *  
		 */
		personSpace(){
			var that = this; 
			that.$http.get(process.env.BASE_URL+"/apps/member/personSpace").then(function(res){ 
				if(res.body.code=="40000"){
					var status =  res.body.list.fail.status;
					status = parseInt(status);
					var text = res.body.list.fail.title;
					var btnTitle = res.body.list.fail.button.title;  
					switch(status){
						case -1: //
							that.$router.push({path: 'not_open_space', query:{type: 0, text:text, btnTitle:btnTitle}});
							break;
						case 1: //
							var cid = res.body.list.cid;
							that.$router.push({path: 'space_detail', query:{type: 0, cid: cid, is_my:1}});
							break;
						case 2: //
							that.$router.push({path: 'not_open_space', query:{type: 0, text:text, btnTitle:btnTitle}});
							break;
						case 3: //
							that.$router.push({path: 'not_open_space', query:{type: 0, text:text, btnTitle:btnTitle}});
							break;
						case 4: //
							var product_id = res.body.list.fail.button.product_id;
							that.$router.push({path: 'not_payment', query:{type: 0, btnTitle:btnTitle,product_id:product_id}});
							break;
						case 5: //
							that.$vux.toast.text(res.body.list.fail.title, 'middle');
							break; 
					} 
				} 
			});
		},
		/**
		 * 
		 */
		companySpace(){
			var that = this; 
			that.$http.get(process.env.BASE_URL+"/apps/member/companySpace").then(function(res){ 
				if(res.body.code=="40000"){
					var status =  res.body.list.fail.status;
					status = parseInt(status);
					var text = res.body.list.fail.title; 
					var btnTitle = res.body.list.fail.button.title;  
					switch(status){
						case -1: //
							that.$router.push({path: 'not_open_space', query:{type: 1, text:text, btnTitle:btnTitle}});
							break;
						case 1: //
							var cid = res.body.list.cid;
							that.$router.push({path: 'space_detail', query:{type: 1, cid: cid, is_my:1}});
							break;
						case 2: //
							that.$router.push({path: 'not_open_space', query:{type: 1, text:text, btnTitle:btnTitle}});
							break;
						case 3: //
							that.$router.push({path: 'not_open_space', query:{type: 1, text:text, btnTitle:btnTitle}});
							break;
						case 4: //
							var product_id = res.body.list.fail.button.product_id;
							that.$router.push({path: 'not_payment', query:{type: 1, btnTitle:btnTitle,product_id:product_id}});
							break;
						case 5: //
							that.$vux.toast.text(res.body.list.fail.title, 'middle');
							break;
						case 6: //
							that.$vux.toast.text(res.body.list.fail.title, 'middle');
							break; 
					} 
				} 
			});
		}
	}
}



